.container {
    height: 80rem;
}

.signinLogo {
    width:300px;
    height:300px;
}

.loginButton {
    width: 50%;
    margin-top: 10px;
    margin-bottom: 10px;
    max-width: 600px;
}

.buttonColorGoogle {
    background-color: #4285F4;
}

.buttonColorKakao {
    color: black;
    background-color: #FEE500;
}

.buttonColorKakao:hover {
    background-color: #b7ac4e;
}

.buttonColorNaver {
    background-color: #03C75A;
}

.buttonColorKakao:hover {
    background-color: #058d41;
}

.signinTitle {
    margin-top: 20px;
    margin-bottom: 20px;
    color: #477F8F;
    font-weight: bolder;
    font-family: 'Gamja Flower';
}

@media screen and (max-width:600px) {
    .container {
        height: 60rem;
    }
}